/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 100rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  padding-top: 5rem;
  margin-bottom: 2rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .header {
    margin-bottom: 4rem;
  }
}

.redes {
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .redes {
    margin: 0;
  }
}
.redes__icono::before {
  content: "";
  display: inline-block;
  padding: 2rem;
}

.barra {
  background-color: #8ed3cc;
  padding: 0.3em 0;
}
.barra__border {
  border-block: 2px dashed #FFFFFF;
  padding: 1.7rem 0;
}
.barra__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1.2rem;
}
@media (min-width: 768px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    row-gap: unset;
  }
}
.barra__enlace {
  color: #FFFFFF;
  font-size: 2rem;
}

.slider {
  overflow: hidden;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}

.bienvenida {
  margin: 4rem 0;
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-weight: 400;
  line-height: 0.7;
  font-size: 4.5rem;
}
.bienvenida__heading span {
  color: #000;
  font-family: "Lato", sans-serif;
  display: block;
  text-transform: uppercase;
}
.bienvenida__texto {
  color: #979797;
  font-size: 2.3rem;
  max-width: 45rem;
  margin: 2rem auto 0 auto;
  line-height: 1.2;
}

.cards {
  overflow: hidden;
}
@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  max-width: 35rem;
  margin: 0 auto 2rem auto;
}
.card:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}
.card__descripcion {
  background-color: #FFFFFF;
  max-width: 90%;
  margin: -5rem auto 0 auto;
  transform: translateY(-5rem);
}
.card__imagen {
  width: 100%;
  border: 5px solid #FFFFFF;
  transition: all 0.4s;
}
.card__imagen:hover {
  transform: rotate(3deg) scale(1.2);
}
.card__titulo {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-weight: 400;
  line-height: 0.7;
  font-size: 4.5rem;
  font-size: 3rem;
  padding: 2rem 2rem 0 2rem;
}
.card__titulo span {
  color: #000;
  font-family: "Lato", sans-serif;
  display: block;
  text-transform: uppercase;
}
.card__boton {
  background-color: #e082a8;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
  padding: 1rem;
}
@media (min-width: 768px) {
  .card__boton {
    width: auto;
  }
}
.card__boton:hover {
  background-color: #d6598c;
}

.horario {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%, transparent 100%), url(../img/bg_horarios.jpg);
  background-repeat: no-repeat;
  background-position: center, bottom center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .horario {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%, transparent 100%), url(../img/bg_horarios.jpg);
    background-position: center, top right;
  }
}
.horario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__contenido {
  padding: 3rem 1rem;
}
.horario__heading {
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .horario__heading {
    text-align: left;
  }
}
.horario__descripcion {
  text-align: justify;
}
.horario__tabla {
  margin-top: 3rem;
  width: 100%;
  border: 0.5rem solid #e082a8;
  text-align: center;
  border-collapse: collapse;
}
.horario__tabla thead {
  background-color: #e082a8;
}
.horario__tabla thead th {
  padding: 1.5rem 0;
  font-size: 2.5rem;
}
.horario__tabla tbody tr:hover {
  background-color: #46b5a9;
}
.horario__tabla tbody td {
  padding: 1rem 0;
  font-size: 2rem;
}

.productos__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-weight: 400;
  line-height: 0.7;
  font-size: 4.5rem;
  margin-bottom: 3rem;
}
.productos__heading span {
  color: #000;
  font-family: "Lato", sans-serif;
  display: block;
  text-transform: uppercase;
}
.productos__grid {
  display: grid;
  grid-template-columns: repeat(8, 30rem);
  column-gap: 3rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
@media (min-width: 480px) {
  .productos__grid {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 3rem;
    overflow-x: hidden;
  }
}
@media (min-width: 768px) {
  .productos__grid {
    grid-template-columns: repeat(3, 1fr);
    overflow-x: hidden;
  }
}

.producto {
  scroll-snap-align: center;
  background-color: #FFFFFF;
}
.producto__contenido {
  padding: 2rem;
}
.producto__mascara {
  overflow: hidden;
}
.producto__imagen {
  width: 100%;
  transition: all 0.4;
}
.producto__imagen:hover {
  transform: scale(1.2);
}
.producto__titulo {
  text-align: center;
  color: #e082a8;
  text-transform: uppercase;
}
.producto__descripcion {
  text-align: justify;
  line-height: 1.3;
}
.producto__precio {
  text-align: center;
  font-weight: bold;
  color: #e082a8;
  font-size: 2.3rem;
}
.producto__precio span {
  font-weight: 400;
  color: #979797;
}

.cita {
  background-image: url(../img/bg_citas.webp);
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  margin: 3rem auto;
}
.cita__contenido {
  color: #FFFFFF;
  padding: 3rem;
  text-align: center;
}
.cita__heading {
  text-transform: uppercase;
}
.cita__texto {
  max-width: 50rem;
  margin: 0 auto;
  line-height: 1.4;
}
.cita__boton {
  background-color: #8ed3cc;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  padding: 1rem 8rem;
}
@media (min-width: 768px) {
  .cita__boton {
    width: auto;
  }
}
.cita__boton:hover {
  background-color: #69c5bb;
}

.footer {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer h3 {
  text-align: center;
}
.footer p {
  text-align: justify;
}
.footer .hora,
.footer .direccion {
  text-align: center;
}
.footer .copy {
  background-color: #8ed3cc;
}
.footer .copy p {
  text-align: center;
  font-weight: bold;
  padding: 2rem;
  color: #FFFFFF;
  margin: 0;
}